import React from 'react'
import {Link} from "react-router-dom"

export default (props)=>(
        <div id="todo">
            <div className="top">
                <span><em>·</em> 代办 </span> 
                <span>|</span>
                <span>你有 <span>{props.message}</span> 条消息待处理！</span>
                <span className="iconfont icon-qianjin"></span>
            </div>
            <ul className="list">
               { props.todoList.map((item,index)=>(
                   <li key={index}>
                     <Link to={item.link}>
                        <img src={item.pic} alt="图片"/>
                        <p>{item.title}</p>
                     </Link>
                   </li>
               ))}
            </ul>
        </div>
)
