import React, { Component } from 'react'
import Myswiper from "../../../common/myswiper/myswiper"
import {withRouter} from "react-router-dom"

const Picker = (props)=>(
    <div className="mui-content">
	  <div className="mui-content-padded">
      <button id='demo6' data-options='{"type":"month"}' className="btn mui-btn mui-btn-block">{props.slectDate}</button>
		<div id='result' className="ui-alert"></div>
	  </div>
    </div>
);

const PunchCard =withRouter((props)=>(
    <>
    <div className="punch" 
    onClick={props.punchAction.bind(this,props)}
    style={{background:props.punch?"#dfdfdf":"#fff"}}>
      <h2>{props.punch?"打卡成功":props.title}</h2>
      <p>{props.currtime}</p>
      <span className={props.punch?"sorce active":"sorce"}>+5</span>
      {props.children}
    </div>
   </>
))

export default class chart extends Component {
    constructor(props){
        super(props);
        this.state = {
            punch:false,
            slideBtn:0,
            currtime:"00:00:00",
            slectDate:"2019-12",
            initiative:[20.0, 14.9, 17.0, 33.2, 25.6, 76.7, 135.6, 162.2, 32.6, 20.0, 63.4, 33.3],
            knowledge:[26, 59, 19, 26.4, 28.7, 70.7, 175.6, 182.2, 48.7, 18.8, 60, 53],
            legendData:['积极性', '解锁知识'],
            xAxisData: ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月']
        }
    }

    monthChange(){
        let {initiative,knowledge} = this.state;
        initiative.sort(()=>(Math.random()-Math.random()));
        knowledge.sort(()=>(Math.random()-Math.random()));
        this.setState({
            initiative,
            knowledge
        });
    }
    
    tranformAct(){
        for(let i=0;i<4;i++){
            let carditem = "carditem"+i;
            this.refs[carditem].setAttribute("class","carditem active");
        }
    }

    getTime(){
        setInterval(() => {
        let date = new Date();
        let h = date.getHours();
        let m = date.getMinutes();
        let s = date.getSeconds();
        h = h<10?"0"+h:h;
        m = m<10?"0"+m:m;
        s = s<10?"0"+s:s;
        this.setState({
            currtime:h+":"+m+":"+s
        }); 
        }, 1000);
    }

    drawEart(){
        let {initiative,knowledge,legendData,xAxisData} = this.state;
        var getOption = function(chartType) {
            var chartOption = chartType === 'pie' || {
                legend: {
                    data: legendData
                },
                grid: {
                    x: 35,
                    x2: 10,
                    y: 30,
                    y2: 25
                },
                toolbox: {
                    show: false,
                    feature: {
                        mark: {
                            show: true
                        },
                        dataView: {
                            show: true,
                            readOnly: false
                        },
                        magicType: {
                            show: true,
                            type: ['line', 'bar']
                        },
                        restore: {
                            show: true
                        },
                        saveAsImage: {
                            show: true
                        }
                    }
                },
                calculable: false,
                xAxis: [{
                    type: 'category',
                    data: xAxisData
                }],
                yAxis: [{
                    type: 'value',
                    splitArea: {
                        show: true
                    }
                }],
                series:[{
                    name: '积极性',
                    type: chartType,
                    data: initiative
                }, {
                    name: '解锁知识',
                    type: chartType,
                    data: knowledge
                }]
            };
            return chartOption;
        };
        var byId = function(id) {
            return document.getElementById(id);
        };
        var barChart = window.echarts.init(byId('barChart'));
        barChart.setOption(getOption('bar'));
        var lineChart = window.echarts.init(byId('lineChart'));
        barChart.setOption(getOption('bar'));
        lineChart.setOption(getOption('line'));
    }

    pickerAction(){
        let chart = this;
        (function($) {
            $.init();
            var btns = $('.btn');
            btns.each(function(i, btn) {
                btn.addEventListener('tap', function() {
                    var _self = this;
                    if(_self.picker) {
                        _self.picker.show(function (rs) {
                            console.log('选择结果: ' + rs.text);
                            _self.picker.dispose();
                            _self.picker = null;
                        });
                    } else {
                        var optionsJson = this.getAttribute('data-options') || '{}';
                        var options = JSON.parse(optionsJson);
                        var id = this.getAttribute('id');
                        _self.picker = new $.DtPicker(options);
                        _self.picker.show(function(rs) {
                            console.log('选择结果: ' + rs.text);
                            (parseInt(rs.text)<parseInt(chart.state.slectDate))&& chart.setState({
                                slectDate: rs.text
                            }); 
                            (parseInt(rs.text)<=parseInt(chart.state.slectDate))&& chart.monthChange.bind(chart)();
                            chart.drawEart.bind(chart)()
                            _self.picker.dispose();
                            _self.picker = null;
                        });
                    }
                    
                }, false);
            });
        })(window.mui);
    }

    tabSlideAction(index){
        window.swiper.slideTo(index, 1000, false);
        this.setState({
            slideBtn:index
        });
    }

    punchAction(){
        this.setState({
            punch:true
        });
        window.mui.toast("获得 5 个积分");
    }

    toStudy(pro){
        pro.history.push("/home/search");
    }

    render() {
        let {currtime,slectDate,slideBtn,punch} = this.state;
        return (
            <div className="chart">
                <Myswiper>
                <div className="swiper-slide swiper-no-swiping">

                    <PunchCard 
                    title="上课打卡"
                    currtime={currtime}
                    punch={punch}
                    punchAction={this.punchAction.bind(this)} 
                    />

                    {[1,2,3,4].map((item,index)=>(<span 
                           className="carditem" 
                           key={item}
                           ref={"carditem"+index}>
                           张{index+1}同学2分钟前打卡
                    </span>))}

                  {/* 日期选择 */}
                  <Picker slectDate={slectDate}/>

                  {/* 柱图示例 */}
				  <div className="chart" id="barChart"></div>

                </div>

                <div className="swiper-slide swiper-no-swiping">
                      <PunchCard
                      title="开始学习"
                      punchAction={this.toStudy.bind(this)}
                      currtime={currtime}/>
                      {/* 日期选择 */}
                      <Picker slectDate={slectDate}/>
                      {/* 折线图 */}
				     <div className="chart" id="lineChart"></div>
                 </div>
               </Myswiper>

               {/* 按钮 */}
               <p className="slideBnt">
                    <span 
                     className={(slideBtn===0)?"active":""} 
                     onClick={this.tabSlideAction.bind(this,0)}>考勤统计</span>
                    <span 
                     className={(slideBtn===1)?"active":""}
                     onClick={this.tabSlideAction.bind(this,1)}>练习统计</span>
              </p>
            </div>
        )
    }

    componentDidMount(){
        this.tranformAct.bind(this)();
        this.getTime();
        this.drawEart();
        this.pickerAction();
    }

    componentWillUnmount(){
        this.tranformAct = null;
    }
}
