import React, { Component } from 'react'
import Header from "./components/header"
import Chart  from './components/chart'
import Todo from "./components/todo"
import Myiscroll from "../../common/myiscroll/myiscroll"
import "./style.scss"

export default class classCom extends Component {
    constructor(props){
        super(props);
        this.state={
            todoList:[
                {
                    title:"课表信息",
                    pic:"/img/1579.png",
                    link:"/schedule"
                },
                {
                    title:"教材讲义",
                    pic:"/img/1614.png",
                    link:"/textbook"
                },
                {
                    title:"教材评价",
                    pic:"/img/1081616.png",
                    link:""
                },
                {
                    title:"集训预约",
                    pic:"/img/42715.png",
                    link:""
                },
                {
                    title:"答疑专区",
                    pic:"/img/1087674.png",
                    link:"/mentoring"
                },
                {
                    title:"报名入口",
                    pic:"/img/42715.png",
                    link:""
                },
                {
                    title:"成绩上传",
                    pic:"/img/579678.png",
                    link:""
                },
            ]
        };
    }
    render() {
        let {todoList} = this.state;
        return (
            <div className="page" id="classCom">
                <Header centent="周末班" right="" self={this.props}/>
                <Myiscroll top="44px">
                  <Chart />
                  <Todo 
                  message={3} 
                  todoList={todoList}/>
                </Myiscroll>
            </div>
        )
    }
}
