import React from 'react'
 

export default (props)=>(
    <header className="border-bottom">
        <span 
        className="iconfont icon-fanhui"
        onClick={()=>(props.self.history.goBack())}></span>
        <div className="centent">
            {props.centent}
        </div>
        <div className="right">
            {props.right}
        </div>
    </header>
)
